import React, {useContext} from 'react';
import clsx from 'clsx';
import {
    AppBar,
    Collapse,
    createStyles,
    CssBaseline,
    Divider,
    Drawer,
    Icon,
    IconButton,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    makeStyles,
    Theme,
    Toolbar,
    Tooltip,
    Typography,
    useTheme,
} from '@material-ui/core';
import {useHistory} from 'react-router-dom';
import MenuNavigation from "./MenuNavigation";
import {useObserver} from 'mobx-react';
import {APPLICATION_FORM, HOME} from "../constants";
import {UserStoreContext} from "../providers/UserProvider";

const drawerWidth = 275;

const useStyles = makeStyles((theme: Theme) => createStyles({
    grow: {
        flexGrow: 1
    },
    resetLinkStyle: {
        textDecoration: 'none',
        color: 'white'
    },
    resetLinkStyleInMenu: {
        textDecoration: 'none',
        color: 'black'
    },
    root: {
        display: 'flex',
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: 'hidden',
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9) + 1,
        },
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    nested: {
        paddingLeft: theme.spacing(3),
    },
    listItemIcon: {
        minWidth: '47px',
    }
}));

export default function Navigation() {
    const classes = useStyles();
    const history = useHistory();
    const userStore = useContext(UserStoreContext);

    const theme = useTheme();
    const [openNavigation, setOpenNavigation] = React.useState(localStorage.getItem('openNavigation') === 'open' ? true : false);
    const [openMenuReg, setOpenMenuReg] = React.useState(true);

    const handleDrawerOpen = () => {
        setOpenNavigation(true);
        localStorage.setItem('openNavigation', 'open');

        setOpenMenuReg(true);
    };

    const handleDrawerClose = () => {
        setOpenNavigation(false);
        localStorage.setItem('openNavigation', 'close');

        setOpenMenuReg(false);
    };

    const handleClickMenuReg = () => {
        setOpenMenuReg(!openMenuReg);
    };

    const openPage = (pageRoute: string) => {
        history.push(pageRoute);
    };

    return useObserver(() => (
        <div className={classes.root}>
            <CssBaseline/>
            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: openNavigation,
                })}
            >
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, {
                            [classes.hide]: openNavigation,
                        })}
                    >
                        <Icon>menu</Icon>
                    </IconButton>

                    <Typography variant="subtitle1">
                        <img alt={'UM iCamp 2020 logo navigation'} src={'./apply/assets/images/um-icamp-2020-glow.png'}
                             height={45}/>
                        &nbsp;&nbsp;
                    </Typography>
                    <Typography className={classes.grow} color="inherit" variant="h6">
                        <span onClick={handleDrawerOpen} style={{cursor: "pointer"}}>
                            {openNavigation ? ' ' : 'UM iCamp 2020'}
                        </span>
                    </Typography>
                    <MenuNavigation/>
                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: openNavigation,
                    [classes.drawerClose]: !openNavigation,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: openNavigation,
                        [classes.drawerClose]: !openNavigation,
                    }),
                }}
            >
                <div style={{height: '64px', display: "flex"}}>
                    <ListItem button onClick={handleDrawerClose}>
                        <ListItemIcon>
                            {theme.direction === 'rtl' ? <Icon>chevron_right</Icon> : <Icon>chevron_left</Icon>}
                        </ListItemIcon>
                        <ListItemText primary="UM iCamp 2020" primaryTypographyProps={{variant: "h6"}}/>
                    </ListItem>
                </div>
                <Divider/>
                <List>
                    <Tooltip disableHoverListener={openNavigation} title="Dashboard" aria-label="dashboard">
                        <ListItem button onClick={() => openPage(HOME)}>
                            <ListItemIcon><Icon>dashboard</Icon></ListItemIcon>
                            <ListItemText primary={"Dashboard"}/>
                        </ListItem>
                    </Tooltip>
                </List>
                <Divider/>
                <Tooltip disableHoverListener={openNavigation} title="Submission" aria-label="submission">
                    <ListItem button onClick={handleClickMenuReg}>
                        <ListItemIcon>
                            <Icon>post_add</Icon>
                        </ListItemIcon>
                        <ListItemText primary="Submission"/>
                        {openMenuReg ? <Icon>expand_less</Icon> : <Icon>expand_more</Icon>}
                    </ListItem>
                </Tooltip>
                <Collapse in={openMenuReg} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        <Tooltip disableHoverListener={openNavigation} title="1. Application Form"
                                 aria-label="application-form">
                            <ListItem button className={classes.nested} onClick={() => openPage(APPLICATION_FORM)}>
                                <ListItemIcon className={classes.listItemIcon}>
                                    <Icon
                                        color={(userStore?.configApplicationForm.isSubmitted ?? false) ? "primary" : 'inherit'}>
                                        {userStore?.applicationFormIsValid ? "check_circle" : "edit"}
                                    </Icon>
                                </ListItemIcon>
                                <ListItemText primary="Application Form"/>
                            </ListItem>
                        </Tooltip>
                        {/*<Tooltip disableHoverListener={openNavigation} title="2. Letter of Commitment"*/}
                        {/*         aria-label="letter-of-commitment">*/}
                        {/*    <ListItem button disabled className={classes.nested}>*/}
                        {/*        <ListItemIcon className={classes.listItemIcon}>*/}
                        {/*            <Icon>lock</Icon>*/}
                        {/*        </ListItemIcon>*/}
                        {/*        <ListItemText primary="2. Letter of Commitment"/>*/}
                        {/*    </ListItem>*/}
                        {/*</Tooltip>*/}
                        {/*<Tooltip disableHoverListener={openNavigation} title="3. Travel Documents"*/}
                        {/*         aria-label="travel-documents">*/}
                        {/*    <ListItem button disabled className={classes.nested}>*/}
                        {/*        <ListItemIcon className={classes.listItemIcon}>*/}
                        {/*            <Icon>lock</Icon>*/}
                        {/*        </ListItemIcon>*/}
                        {/*        <ListItemText primary="3. Travel Documents"/>*/}
                        {/*    </ListItem>*/}
                        {/*</Tooltip>*/}
                    </List>
                </Collapse>
                <Divider/>
            </Drawer>
        </div>
    ));
}