import React from 'react';
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import {ICountryType} from "../../constants/interfaces";
import {countries, countryToFlag, countryToICountryType} from "../../constants";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles({
    option: {
        fontSize: 15,
        '& > span': {
            marginRight: 10,
            fontSize: 18,
        },
    },
});

interface IInputAutoComplete {
    value?: string,
    label: string,
    id?: string,
    onChange?: (event: React.ChangeEvent<{}>, value: any | null) => void;
}

const InputAutoCompleteForCountry: React.FC<IInputAutoComplete> = ({
                                                                       value,
                                                                       label,
                                                                       id,
                                                                       onChange,
                                                                   }) => {
    const classes = useStyles();

    return (
        <Autocomplete
            id="nationality-select-demo"
            style={{width: '100%', marginTop: 16, marginBottom: 8}}
            options={countries as ICountryType[]}
            classes={{option: classes.option}}
            onChange={onChange}
            value={countryToICountryType(value)}
            autoHighlight
            disableClearable={true}
            getOptionLabel={option => option.label}
            renderOption={option => (
                <React.Fragment>
                    <span>{countryToFlag(option.code)}</span>
                    {option.label} ({option.code}) +{option.phone}
                </React.Fragment>
            )}
            renderInput={(params: any) => (
                <TextField
                    {...params}
                    required
                    id={id}
                    name={id}
                    label={label}
                    variant="outlined"
                    inputProps={{
                        ...params.inputProps,
                        autoComplete: 'nationality', // disable autocomplete and autofill
                    }}
                />
            )}
        />);
};

export default InputAutoCompleteForCountry;