import React, {useContext} from 'react';
import {makeStyles, Theme} from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Navigation from "../components/Navigation";
import {Divider, Grid, Icon, PropTypes} from "@material-ui/core";
import {PersonalDetails} from "../components/application-form/PersonalDetails";
import {useObserver} from 'mobx-react';
import {EducationBackground} from "../components/application-form/EducationBackground";
import {HealthCondition} from "../components/application-form/HealthCondition";
import {PersonalStatement} from "../components/application-form/PersonalStatement";
import {EmergencyContactDetail} from "../components/application-form/EmergencyContact";
import {UserStoreContext} from "../providers/UserProvider";
import {ApplicantMedia} from "../components/application-form/ApplicantMedia";
import {ApplicationFormConfirm} from "../components/application-form/ApplicationFormConfirm";

interface TabPanelProps {
    children?: React.ReactNode;
    index: any;
    value: any;
}

function TabPanel(props: TabPanelProps) {
    const {children, value, index, ...other} = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

function a11yProps(index: any) {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        display: 'flex',
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        paddingTop: theme.spacing(3),
        paddingRight: theme.spacing(3),
        paddingBottom: theme.spacing(3),
    },
}));

export default function ApplicationForm() {
    const classes = useStyles();
    const userStore = useContext(UserStoreContext);
    const [value, setValue] = React.useState(0);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    const colorTileItem = (value?: boolean): PropTypes.Color => {
        if (value) {
            if (userStore?.configApplicationForm.isSubmitted) return 'primary';
            return 'secondary';
        }
        return 'inherit';
    };

    return useObserver(() => (<div className={classes.root}>
        <Navigation/>

        <main className={classes.content}>
            <div className={classes.toolbar}/>

            <Grid container>
                <Grid item xs={4} sm={2}>
                    <Tabs
                        orientation="vertical"
                        variant="scrollable"
                        indicatorColor="primary"
                        textColor="primary"
                        value={value}
                        onChange={handleChange}
                        aria-label="Vertical tabs example"
                        className={classes.tabs}
                    >
                        <Tab icon={<Icon>emoji_emotions</Icon>} label="Information" {...a11yProps(0)} />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.personalDetailsIsValid)}>
                                    {userStore?.personalDetailsIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 1" {...a11yProps(1)}
                        />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.educationBackgroundIsValid)}>
                                    {userStore?.educationBackgroundIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 2" {...a11yProps(1)}
                        />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.healthConditionIsValid)}>
                                    {userStore?.healthConditionIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 3" {...a11yProps(1)}
                        />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.personalStatementIsValid)}>
                                    {userStore?.personalStatementIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 4" {...a11yProps(1)}
                        />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.emergencyContactIsValid)}>
                                    {userStore?.emergencyContactIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 5" {...a11yProps(1)}
                        />
                        <Tab
                            // disabled={userStore?.configApplicationForm.isSubmitted}
                            icon={
                                <Icon
                                    color={colorTileItem(userStore?.applicantMediaIsValid)}>
                                    {userStore?.applicantMediaIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                                </Icon>}
                            label="Section 6" {...a11yProps(1)}
                        />
                        <Tab disabled={!userStore?.applicationFormIsValid} icon={
                            <Icon>
                                {userStore?.applicantMediaIsValid ? 'emoji_emotions' : 'sentiment_satisfied'}
                            </Icon>}
                             label="Confirmation" {...a11yProps(1)}
                        />
                    </Tabs>
                </Grid>
                <Grid item xs={8} sm={10}>
                    <TabPanel value={value} index={0}>
                        <Grid item>
                            <Typography variant="h3" gutterBottom>
                                Application Form
                            </Typography>
                            <Divider/>
                            <Typography variant="body1" gutterBottom>
                                Before starting your application, please prepare the following documents:
                            </Typography>
                            <ul>
                                <li>Short essay about your motivation in applying for UM iCamp 2020 what knowledge
                                    you
                                    seek to gain,
                                    and how UM iCamp program will benefit from your attendance. (max. 500 words)
                                </li>
                                <li>Short essay about your activities or experiences in your community. (max. 500
                                    words)
                                </li>
                                <li>Short argument in aspects that make you the most suitable candidate for the
                                    program.
                                </li>
                                <li>A Formal Photo in Colour (passport size photo 4 cm x 6 cm). (File
                                    .JPG
                                    max. 1 MB)
                                </li>
                                <li>Scan of passport. (File .JPG max. 1 MB)</li>
                                <li>A medical certificate. (File .PDF max. 1 MB)</li>
                                <li>Please fill out Declaration form in this link&nbsp;
                                    <a href={'http://bit.ly/declarationletter'} target={'_blank'}
                                       rel="noopener noreferrer">http://bit.ly/declarationletter</a>, sign, and scan it
                                    in PDF file format. (max. 1 MB)
                                </li>
                            </ul>
                            <Typography variant="body1" gutterBottom>
                                You can save and revise those documents anytime before
                                clicking <strong>SUBMIT</strong> at the end of the &nbsp;
                                <strong>CONFIRMATION SECTION</strong>. Please be advised that the submission deadline is
                                on 1 May 2020 at
                                23.59 (GMT+7).
                            </Typography>
                        </Grid>
                    </TabPanel>
                    <TabPanel value={value} index={1}>
                        <PersonalDetails/>
                    </TabPanel>
                    <TabPanel value={value} index={2}>
                        <EducationBackground/>
                    </TabPanel>
                    <TabPanel value={value} index={3}>
                        <HealthCondition/>
                    </TabPanel>
                    <TabPanel value={value} index={4}>
                        <PersonalStatement/>
                    </TabPanel>
                    <TabPanel value={value} index={5}>
                        <EmergencyContactDetail/>
                    </TabPanel>
                    <TabPanel value={value} index={6}>
                        <ApplicantMedia/>
                    </TabPanel>
                    <TabPanel value={value} index={7}>
                        <ApplicationFormConfirm/>
                    </TabPanel>
                </Grid>
            </Grid>
        </main>
    </div>));
}





