import React, {useContext, useEffect, useState} from "react";
import {Divider, Grid, Typography} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import {UserStoreContext} from "../../providers/UserProvider";
import InputUploadMedia, {AcceptedFileTypes, ExampleTypeOptions} from "../forms/InputUploadMedia";

enum InputType {
    passportPhoto,
    passportScan,
    medicalCertificate,
    declarationLetter,
}

export const ApplicantMedia: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);

    const [passportPhoto, setPassportPhoto] = useState<string | undefined>();
    const [passportScan, setPassportScan] = useState<string | undefined>();
    const [medicalCertificate, setMedicalCertificate] = useState<string | undefined>();
    const [declarationLetter, setDeclarationLetter] = useState<string | undefined>();

    useEffect(() => {
        if (!loading) {
            setPassportPhoto(userStore?.applicantMedia?.passportPhoto);
            setPassportScan(userStore?.applicantMedia?.passportScan);
            setMedicalCertificate(userStore?.applicantMedia?.medicalCertificate);
            setDeclarationLetter(userStore?.applicantMedia?.declarationLetter);
        }
        // eslint-disable-next-line
    }, [loading]);

    const onSubmittedMedia = async (error: boolean, inputType: InputType, url?: string) => {
        if (!error) {
            setLoading(true);
            let hasError: boolean = false;

            switch (inputType) {
                case InputType.passportPhoto:
                    hasError = (await userStore?.updateApplicantMedia({
                        passportPhoto: url,
                        passportScan: passportScan,
                        medicalCertificate: medicalCertificate,
                        declarationLetter: declarationLetter,
                    }))?.hasError() ?? false;
                    break;
                case InputType.passportScan:
                    hasError = (await userStore?.updateApplicantMedia({
                        passportPhoto: passportPhoto,
                        passportScan: url,
                        medicalCertificate: medicalCertificate,
                        declarationLetter: declarationLetter,
                    }))?.hasError() ?? false;
                    break;
                case InputType.medicalCertificate:
                    hasError = (await userStore?.updateApplicantMedia({
                        passportPhoto: passportPhoto,
                        passportScan: passportScan,
                        medicalCertificate: url,
                        declarationLetter: declarationLetter,
                    }))?.hasError() ?? false;
                    break;
                case InputType.declarationLetter:
                    hasError = (await userStore?.updateApplicantMedia({
                        passportPhoto: passportPhoto,
                        passportScan: passportScan,
                        medicalCertificate: medicalCertificate,
                        declarationLetter: url,
                    }))?.hasError() ?? false;
                    break;
            }
            if (hasError) {
                toast.error(`Oops, we've got a problem`);
            } else {
                toast.success("Success updating Applicant Media");
            }
            setLoading(false);
        }
    };

    return useObserver(() => {
        return (<>
            {
                // @ts-ignore
                <LoadingBackground open={loading || userStore?.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Applicant Media
                </Typography>
                <Divider/>
                <form style={{width: '80%', textAlign: 'center'}}>
                    <InputUploadMedia
                        disabled={userStore?.configApplicationForm.isSubmitted}
                        dialogTitle={"Passport size photo *"}
                        subHeader={'(4x6 cm)'}
                        fileName={'passport-photo'}
                        value={passportPhoto}
                        showExampleButton={true}
                        exampleType={ExampleTypeOptions.ASSETS_IMAGE}
                        exampleData={'apply/assets/images/passport-photo-example.png'}
                        onSubmitted={(message, url, error) => onSubmittedMedia(error ?? false, InputType.passportPhoto, url)}
                    />
                    <InputUploadMedia
                        disabled={userStore?.configApplicationForm.isSubmitted}
                        dialogTitle={"Scan of passport *"}
                        fileName={'passport-scan'}
                        value={passportScan}
                        showExampleButton={true}
                        exampleType={ExampleTypeOptions.ASSETS_IMAGE}
                        exampleData={'apply/assets/images/passport-scan-example.png'}
                        onSubmitted={(message, url, error) => onSubmittedMedia(error ?? false, InputType.passportScan, url)}
                    />
                    <InputUploadMedia
                        disabled={userStore?.configApplicationForm.isSubmitted}
                        dialogTitle={"Medical certificate *"}
                        fileName={'medical-certificate'}
                        value={medicalCertificate}
                        acceptedFileType={AcceptedFileTypes.PDF}
                        onSubmitted={(message, url, error) => onSubmittedMedia(error ?? false, InputType.medicalCertificate, url)}
                    />
                    <InputUploadMedia
                        disabled={userStore?.configApplicationForm.isSubmitted}
                        dialogTitle={"Declaration letter *"}
                        fileName={'declaration-letter'}
                        value={declarationLetter}
                        acceptedFileType={AcceptedFileTypes.PDF}
                        showExampleButton={true}
                        exampleType={ExampleTypeOptions.LINK}
                        exampleData={'https://drive.google.com/file/d/1XY4gbHkBv-VbjLsIJ5AiwmBWOk8QXZqH/view'}
                        onSubmitted={(message, url, error) => onSubmittedMedia(error ?? false, InputType.declarationLetter, url)}
                    />
                </form>
            </Grid>
        </>);
    });
};

