import React, {MouseEventHandler} from "react";
import Button from "@material-ui/core/Button";
import {Chip} from "@material-ui/core";

interface IInputSubmit {
    label?: string,
    onClick?: MouseEventHandler<any>;
    disabled?: boolean;
    showNextSectionWarning?: boolean
}

const InputSubmit: React.FC<IInputSubmit> = ({
                                                 label = "Submit",
                                                 onClick,
                                                 disabled,
                                                 showNextSectionWarning = true,
                                             }) => {

    return (<>
        <br/>
        {
            showNextSectionWarning && !disabled ? <>
                <br/>
                <Chip
                    label="Don't forget to click SAVE button before moving to the next/previous section"
                    variant="outlined"
                    color="secondary"

                />
                <br/>
            </> : <></>
        }
        <br/>
        <Button
            disabled={disabled}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            onClick={onClick}
        >
            {label}
        </Button>
    </>);
};

export default InputSubmit;