import React, {FormEventHandler, useContext, useEffect, useState} from "react";
import {Divider, Grid, Typography} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import InputTextField from "../forms/InputTextField";
import {dietaryRestrictionOptions, disabilityOptions} from "../../constants/options";
import InputSubmit from "../forms/InputSubmit";
import InputCheckboxGroup from "../forms/InputCheckboxGroup";
import {IInputRadioGroupData} from "../../constants/interfaces";
import InputRadioGroup from "../forms/InputRadioGroup";
import {UserStoreContext} from "../../providers/UserProvider";

export const HealthCondition: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);


    const [dietaryRestriction, setDietaryRestriction] = useState<string[] | undefined>();
    const [dietaryDetail, setDietaryDetail] = useState<string | undefined>();
    const [disability, setDisability] = useState<string | undefined>();
    const [disabilityDetail, setDisabilityDetail] = useState<string | undefined>();
    const [medicalCondition, setMedicalCondition] = useState<string | undefined>();
    const [specialRequirements, setSpecialRequirements] = useState<string | undefined>();


    useEffect(() => {
        if (!loading) {
            setDietaryRestriction(userStore?.healthCondition?.dietaryRestriction);
            setDietaryDetail(userStore?.healthCondition?.dietaryDetail);
            setDisability(userStore?.healthCondition?.disability ?? disabilityOptions[1]);
            setDisabilityDetail(userStore?.healthCondition?.disabilityDetail);
            setMedicalCondition(userStore?.healthCondition?.medicalCondition);
            setSpecialRequirements(userStore?.healthCondition?.specialRequirements);
        }
        // eslint-disable-next-line
    }, [loading]);


    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();

        setLoading(true);
        const res = await userStore?.updateHealthCondition({
            dietaryRestriction,
            dietaryDetail,
            disability,
            disabilityDetail,
            medicalCondition,
            specialRequirements,
        });
        if (res?.hasError()) {
            toast.error(res?.error?.message);
            setLoading(false);
        } else {
            toast.success("Success updating Health Condition");
        }

        setLoading(false);
    };

    return useObserver(() => {
        return (<>
            {
                // @ts-ignore
                <LoadingBackground open={loading || userStore.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Dietary and Health Condition
                </Typography>
                <Divider/>
                <form onSubmit={onSubmitHandler} style={{width: '80%', textAlign: 'center'}}>
                    <InputCheckboxGroup
                        label={'Dietary Restriction'}
                        options={dietaryRestrictionOptions}
                        values={dietaryRestriction ?? []}
                        onChange={(data) => setDietaryRestriction(data)}
                        required={true}
                    />
                    <InputTextField
                        value={dietaryDetail}
                        id={'dietaryDetail'}
                        label={"For other or specific dietary, please specify here"}
                        onChange={(e) => setDietaryDetail(e.currentTarget.value)}
                        multiline={true}
                    />
                    <InputRadioGroup
                        options={disabilityOptions.map((data) => {
                            return {
                                label: data,
                                value: data,
                            } as IInputRadioGroupData;
                        })}
                        label={'Do you have any disability?'}
                        id={'disabilitylatestDegree'}
                        value={disability ?? disabilityOptions[1]}
                        onChange={(e) => setDisability(e.currentTarget.value)}
                        required={true}
                    />
                    <InputTextField
                        value={disabilityDetail}
                        id={'disabilityDetail'}
                        label={"If you have any disabilities, please specify here"}
                        onChange={(e) => setDisabilityDetail(e.currentTarget.value)}
                        multiline={true}
                    />
                    <InputTextField
                        value={medicalCondition}
                        id={'medicalCondition'}
                        label={"If you have any medical condition to be aware of, please specify here"}
                        // helperText={' from and any medication taken. (if any)'}
                        onChange={(e) => setMedicalCondition(e.currentTarget.value)}
                        multiline={true}
                    />
                    <InputTextField
                        value={specialRequirements}
                        id={'specialRequirements'}
                        label={"If you have any allergies or sensitivity, please specify here"}
                        // helperText={"Any issues we should be aware of"}
                        onChange={(e) => setSpecialRequirements(e.currentTarget.value)}
                        multiline={true}
                    />
                    <InputSubmit disabled={userStore?.configApplicationForm.isSubmitted}
                                 label={"Save"} onClick={onSubmitHandler}/>
                </form>
            </Grid>
        </>);
    });
};