import React, {FormEventHandler, useContext, useEffect, useState} from "react";
import {Divider, Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import {IInputRadioGroupData} from "../../constants/interfaces";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import InputTextField from "../forms/InputTextField";
import InputRadioGroup from "../forms/InputRadioGroup";
import {countries, degreeOptions} from "../../constants/options";
import InputAutoCompleteForCountry from "../forms/InputAutoCompleteForCountry";
import InputSubmit from "../forms/InputSubmit";
import {UserStoreContext} from "../../providers/UserProvider";

export const EducationBackground: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);

    const [latestDegree, setLatestDegree] = useState<string | undefined>();
    const [homeUniversity, setHomeUniversity] = useState<string | undefined>();
    const [faculty, setFaculty] = useState<string | undefined>();
    const [department, setDepartment] = useState<string | undefined>();
    const [country, setCountry] = useState<string | undefined>();

    useEffect(() => {
        if (!userStore?.loading) {
            setLatestDegree(userStore?.educationBackground?.latestDegree ?? degreeOptions[0]);
            setHomeUniversity(userStore?.educationBackground?.homeUniversity);
            setFaculty(userStore?.educationBackground?.faculty);
            setDepartment(userStore?.educationBackground?.department);
            setCountry(userStore?.educationBackground?.country ?? countries[99].label);
        }
    }, [loading, userStore]);


    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();
        if (!e?.currentTarget?.form?.reportValidity()) {
            toast.info("Please complete all required data");
            return;
        }

        setLoading(true);
        const res = await userStore?.updateEducationBackground({
            latestDegree,
            homeUniversity,
            faculty,
            department,
            country,
        });
        if (res?.hasError()) {
            toast.error(res?.error?.message);
            setLoading(false);
        } else {
            toast.success("Success updating Education Background");
        }

        setLoading(false);
    };


    return useObserver(() => {
        if (userStore?.loading)
            return <LoadingBackground open={true}/>;

        return (<>
            {
                // @ts-ignore}
                <LoadingBackground open={loading || userStore.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Education Background
                </Typography>
                <Divider/>
                <form onSubmit={onSubmitHandler} style={{width: '80%', textAlign: 'center'}} noValidate={false}>
                    <InputRadioGroup
                        options={degreeOptions.map((data) => {
                            return {
                                label: data,
                                value: data,
                            } as IInputRadioGroupData;
                        })}
                        label={'Latest degree'}
                        id={'latestDegree'}
                        value={latestDegree ?? degreeOptions[0]}
                        onChange={(e) => setLatestDegree(e.currentTarget.value)}
                        required={true}
                    />
                    <InputTextField
                        value={homeUniversity}
                        id={'homeUniversity'}
                        label={"Home University / Institution / Affiliation"}
                        onChange={(e) => setHomeUniversity(e.currentTarget.value)}
                        required={true}
                    />
                    <InputTextField
                        value={faculty}
                        id={'faculty'}
                        label={"Faculty / Division"}
                        onChange={(e) => setFaculty(e.currentTarget.value)}
                    />
                    <InputTextField
                        value={department}
                        id={'department'}
                        label={"Department / Position"}
                        onChange={(e) => setDepartment(e.currentTarget.value)}
                    />
                    <InputAutoCompleteForCountry
                        label={"Country"}
                        value={country ?? countries[99].label}
                        onChange={(e: any, v: any) => setCountry(v?.label)}
                    />

                    <InputSubmit disabled={userStore?.configApplicationForm.isSubmitted}
                                 label={"Save"} onClick={onSubmitHandler}/>
                </form>
            </Grid>
        </>);
    });
};