import React, {useContext, useState} from "react";
import {
    Button,
    Card,
    CardContent,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Divider,
    Grid,
    Typography
} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import {useObserver} from 'mobx-react';
import {UserStoreContext} from "../../providers/UserProvider";
import {toast} from "react-toastify";
import useEmailService from "../../utils/useEmailService";
import {TEST_CONFIRMATION_TEMPLATE} from "../../constants/emailTemplate";

export const ApplicationFormConfirm: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);
    const {sendEmail} = useEmailService;

    const [openDialog, setOpenDialog] = useState<boolean>(false);

    const handleOpen = async () => {
        setOpenDialog(true);
    };

    const handleCancel = () => {
        setOpenDialog(false);
    };

    const handleOk = async () => {
        handleCancel();
        setLoading(true);
        try {
            const configData = userStore?.configApplicationForm;

            const resSendEmail = await sendEmail({
                to: localStorage.getItem('email') ?? '',
                subject: 'UM iCamp 2020 - Your Registration is Confirmed!',
                content: TEST_CONFIRMATION_TEMPLATE(userStore?.personalDetails.fullName),
            });

            if (resSendEmail?.hasError())
                throw new Error();

            const res = await userStore?.updateConfigApplicationForm({
                isSubmitted: true,
                responseStatus: configData?.responseStatus,
                messageFromAdmin: configData?.messageFromAdmin,
                messageToAdmin: configData?.messageToAdmin,
                dateMessageFromAdmin: configData?.dateMessageFromAdmin,
                dateMessageToAdmin: configData?.dateMessageToAdmin,
            });

            if (res?.hasError())
                throw new Error();

            toast.success("Success submitting Application Form");
        } catch (e) {
            toast.error(`Oops, we've got a problem`);
        }

        setLoading(false);
    };

    return useObserver(() => {
        return (<>
            {
                // @ts-ignore
                <LoadingBackground open={loading || userStore?.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Confirmation
                </Typography>
                <Divider/>
                <br/> <br/>
                <Grid item style={{
                    textAlign: "center",
                    maxWidth: '65%',
                    minWidth: '400px',
                    margin: 'auto',
                }}>
                    <Card color={'red'}>
                        <CardContent>
                            <Typography variant={"h6"}>
                                DISCLAIMER
                            </Typography>
                            <Divider/>
                            <br/>
                            <Typography variant={"body1"}>
                                Please ensure that you have put and uploaded the right data and documents before
                                clicking submit button.
                                Once you click <strong>SUBMIT</strong>, you <strong>CANNOT</strong> change your
                                data/documents.
                            </Typography>
                        </CardContent>
                    </Card>
                    <br/>
                    <br/>
                    <Button
                        color={"primary"}
                        size={"medium"}
                        variant={"contained"}
                        onClick={handleOpen}
                        disabled={userStore?.configApplicationForm.isSubmitted ?? false}
                    >
                        Submit
                    </Button>
                </Grid>
            </Grid>
            <Dialog
                maxWidth="sm"
                aria-labelledby="confirmation-dialog-title"
                open={openDialog}
            >
                <DialogTitle id="confirmation-dialog-title">Final confirmation</DialogTitle>
                <Divider/>
                <DialogContent>
                    <Typography variant={'body2'}>
                        Once you click <strong>SUBMIT</strong>, you <strong>CANNOT</strong> change your
                        data/documents.
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleCancel} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={handleOk} variant={'contained'} color="primary">
                        Submit
                    </Button>
                </DialogActions>
            </Dialog>
        </>);
    });
};
