import React, {useEffect, useState} from 'react';
import {Checkbox, createStyles, FormControl, FormControlLabel, FormGroup, FormLabel} from "@material-ui/core";
import {makeStyles} from "@material-ui/core/styles";
import cuid from "cuid";

const useStyles = makeStyles(() =>
    createStyles({
        formControl: {
            width: '100%',
            marginTop: 16,
            marginBottom: 8,
            textAlign: 'left',
        },
    }),
);

interface IInputCheckboxGroup {
    values: string[],
    options: string[],
    onChange: (value: string[]) => void,
    label?: string,
    required?: boolean,
}

interface IState {
    value: string,
    check: boolean,
}

const InputCheckboxGroup: React.FC<IInputCheckboxGroup> = ({
                                                               values,
                                                               options,
                                                               label = "Label",
                                                               onChange,
                                                               required = false,
                                                           }) => {
    const classes = useStyles();
    const [state, setState] = useState<IState[]>(options.map((data) => {
        return {
            value: data,
            check: false,
        } as IState;
    }));

    useEffect(() => {
        if (values?.length > 0)
            setState(state.map((data) => {
                values?.forEach((val) => data.check = val === data.value ? true : data.check);
                return data;
            }));
        // eslint-disable-next-line
    }, [values]);

    const handleChange = (selectedData: IState) => () => {
        setState(state.map((data) => {
            if (selectedData === data) {
                selectedData.check = !selectedData.check;
                return selectedData;
            }

            return data;
        }));

        values = state.filter((data) => data.check).map((data) => data.value);
        onChange(values);
    };

    return (
        <FormControl
            required={required}
            component="fieldset"
            className={classes.formControl}
        >
            <FormLabel component="legend">{label}</FormLabel>
            <FormGroup>
                {state.map((data) => (
                    <FormControlLabel
                        key={cuid()}
                        control={<Checkbox checked={data.check} onChange={handleChange(data)} value={data.value}/>}
                        label={data.value}
                    />
                ))}
            </FormGroup>
        </FormControl>
    );
};

export default InputCheckboxGroup;