import React, {FormEventHandler, useContext, useEffect, useState} from "react";
import {Divider, Grid, Typography} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import InputTextField from "../forms/InputTextField";
import {countries} from "../../constants";
import InputSubmit from "../forms/InputSubmit";
import {UserStoreContext} from "../../providers/UserProvider";
import InputAutoCompleteForCountry from "../forms/InputAutoCompleteForCountry";
import InputPhone from "../forms/InputPhone";

export const EmergencyContactDetail: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);

    const [fullName, setFullName] = useState<string | undefined>();
    const [relationship, setRelationship] = useState<string | undefined>();
    const [email, setEmail] = useState<string | undefined>();
    const [phone, setPhone] = useState<string | undefined>();
    const [address, setAddress] = useState<string | undefined>();
    const [city, setCity] = useState<string | undefined>();
    const [province, setProvince] = useState<string | undefined>();
    const [country, setCountry] = useState<string | undefined>();
    const [postcode, setPostcode] = useState<string | undefined>();

    useEffect(() => {
        if (!loading) {
            setFullName(userStore?.emergencyContact?.fullName);
            setRelationship(userStore?.emergencyContact?.relationship);
            setEmail(userStore?.emergencyContact?.email);
            setPhone(userStore?.emergencyContact?.phone);
            setAddress(userStore?.emergencyContact?.address);
            setCity(userStore?.emergencyContact?.city);
            setProvince(userStore?.emergencyContact?.province);
            setCountry(userStore?.emergencyContact?.country ??countries[99].label);
            setPostcode(userStore?.emergencyContact?.postcode);
        }
        // eslint-disable-next-line
    }, [loading]);


    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();

        if ((phone?.length ?? 0) < 3) {
            toast.info("Please check again your phone number");
            return;
        }

        if (!e?.currentTarget?.form?.reportValidity()) {
            toast.info("Please complete all required data");
            return;
        }

        setLoading(true);

        const res = await userStore?.updateEmergencyContact({
            fullName,
            relationship,
            email,
            phone,
            address,
            city,
            province,
            country,
            postcode,
        });
        if (res?.hasError()) {
            toast.error(res?.error?.message);
            setLoading(false);
        } else {
            toast.success("Success updating Emergency Contact");
        }

        setLoading(false);
    };

    return useObserver(() => {
        return (<>
            {
                // @ts-ignore
                <LoadingBackground open={loading || userStore.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Emergency Contact Detail
                </Typography>
                <Divider/>
                <form onSubmit={onSubmitHandler} style={{width: '80%', textAlign: 'center'}}>

                    <InputTextField
                        value={fullName}
                        id={'fullName'}
                        label={"Full name"}
                        onChange={(e) => setFullName(e.currentTarget.value)}
                        required={true}
                    />
                    <InputTextField
                        value={relationship}
                        id={'relationship'}
                        label={"Relationship to participant"}
                        onChange={(e) => setRelationship(e.currentTarget.value)}
                        required={true}
                    />
                    <InputTextField
                        value={email}
                        id={'email'}
                        label={"E-mail"}
                        onChange={(e) => setEmail(e.currentTarget.value)}
                        required={true}
                        type={'email'}
                    />
                    <InputPhone
                        required
                        labels={'Phone or WhatsApp number (please include the country code)'}
                        value={phone ?? ''}
                        onChange={setPhone}
                    />
                    <br/>
                    <InputTextField
                        value={address}
                        id={'address'}
                        label={"Address"}
                        onChange={(e) => setAddress(e.currentTarget.value)}
                        required={true}
                        multiline={true}
                    />
                    <InputTextField
                        value={city}
                        id={'city'}
                        label={"City"}
                        onChange={(e) => setCity(e.currentTarget.value)}
                        required={true}
                        multiline={true}
                    />
                    <InputTextField
                        value={province}
                        id={'province'}
                        label={"Province"}
                        onChange={(e) => setProvince(e.currentTarget.value)}
                        required={true}
                    />
                    <InputAutoCompleteForCountry
                        label={"Country"}
                        value={country ?? countries[99].label}
                        onChange={(e: any, v: any) => setCountry(v?.label)}
                    />
                    <InputTextField
                        value={postcode}
                        id={'postcode'}
                        label={"Postcode"}
                        onChange={(e) => setPostcode(e.currentTarget.value)}
                        required={true}
                    />
                    <InputSubmit
                        disabled={userStore?.configApplicationForm.isSubmitted}
                        label={"Save"}
                        onClick={onSubmitHandler}/>
                </form>
            </Grid>
        </>);
    });
};

