import React, {FormEventHandler, useContext, useEffect, useState} from "react";
import {Divider, Grid, Typography} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import InputTextField from "../forms/InputTextField";
import {informationSourceOptions} from "../../constants/options";
import InputSubmit from "../forms/InputSubmit";
import InputCheckboxGroup from "../forms/InputCheckboxGroup";
import {UserStoreContext} from "../../providers/UserProvider";

export const PersonalStatement: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);


    const [informationSources, setInformationSources] = useState<string[] | undefined>();
    const [motivation, setMotivation] = useState<string | undefined>();
    const [additionalInformationSources, setAdditionalInformationSources] = useState<string | undefined>();
    const [personalExperiences, setPersonalExperiences] = useState<string | undefined>();
    const [specialitiesDescribe, setSpecialitiesDescribe] = useState<string | undefined>();
    const [afterProgram, setAfterProgram] = useState<string | undefined>();
    const [othersIsChecked, setOthersIsChecked] = useState<boolean>(false);


    useEffect(() => {
        if (!loading) {
            setInformationSources(userStore?.personalStatement?.informationSources);
            setAdditionalInformationSources(userStore?.personalStatement?.additionalInformationSources);
            setMotivation(userStore?.personalStatement?.motivation);
            setPersonalExperiences(userStore?.personalStatement?.personalExperiences);
            setSpecialitiesDescribe(userStore?.personalStatement?.specialitiesDescribe);
            setAfterProgram(userStore?.personalStatement?.afterProgram);
        }
        // eslint-disable-next-line
    }, [loading]);


    function isValueError(val?: string, showToast: boolean = false, messageToast: string = 'val'): boolean {
        if (val) {
            if (val?.split(' ')?.length > 500) {
                if (showToast) toast.error(`Please check your ${messageToast} field again`);
                return true;
            }
        }
        return false;
    }

    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();


        if (!e?.currentTarget?.form?.reportValidity()) {
            toast.info("Please complete all required data");
            return;
        }


        if (
            isValueError(motivation, true, 'motivation') ||
            isValueError(personalExperiences, true, 'personal experiences')
        ) {
            return;
        }

        setLoading(true);
        const res = await userStore?.updatePersonalStatement({
            informationSources,
            motivation,
            personalExperiences,
            specialitiesDescribe,
            afterProgram,
            additionalInformationSources,
        });
        if (res?.hasError()) {
            toast.error(res?.error?.message);
            setLoading(false);
        } else {
            toast.success("Success updating Personal Statement");
        }

        setLoading(false);
    };

    useEffect(() => {
        if (informationSources) {
            if (informationSources.includes(`Other's`))
                setOthersIsChecked(true);
            else
                setOthersIsChecked(false);
        }
    }, [informationSources]);

    return useObserver(() => {
        return (<>
            {
                // @ts-ignore
                <LoadingBackground open={loading || userStore.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Personal Statement
                </Typography>
                <Divider/>
                <form onSubmit={onSubmitHandler} style={{width: '80%', textAlign: 'center'}}>
                    <InputTextField
                        value={motivation}
                        id={'motivation'}
                        label={"Please tell us your motivation for applying *"}
                        // helperText={"What knowledge you seek to gain, and how UM iCamp program would benefit from your attendance (max. 500 words)*"}
                        helperText={"max. 500 words"}
                        onChange={(e) => setMotivation(e.currentTarget.value)}
                        multiline={true}
                        // required={true}
                        rows={3}
                        rowsMax={8}
                        error={isValueError(motivation)}
                    />
                    <InputTextField
                        value={personalExperiences}
                        id={'personalExperiences'}
                        label={"Please share your volunteering or extra curricular experience in school or your community *"}
                        // helperText={" As it relates to the previous short essay (max. 500 words)*"}
                        helperText={"max. 500 words"}
                        onChange={(e) => setPersonalExperiences(e.currentTarget.value)}
                        multiline={true}
                        // required={true}
                        rows={3}
                        rowsMax={8}
                        error={isValueError(personalExperiences)}
                    />
                    <InputTextField
                        value={specialitiesDescribe}
                        id={'specialitiesDescribe'}
                        label={"Your achievements, traits, uniqueness, or specialities (why we need to choose you as a participant)"}
                        // helperText={"That makes you the most suitable candidate for the program"}
                        onChange={(e) => setSpecialitiesDescribe(e.currentTarget.value)}
                        multiline={true}
                        required={true}
                        rows={3}
                        rowsMax={8}
                    />
                    <InputTextField
                        value={afterProgram}
                        id={'afterProgram'}
                        label={"What is your plan after participating in this program?"}
                        onChange={(e) => setAfterProgram(e.currentTarget.value)}
                        multiline={true}
                        required={true}
                        rows={3}
                        rowsMax={8}
                    />
                    <InputCheckboxGroup
                        label={'How did you hear about UM iCamp 2020?'}
                        options={informationSourceOptions}
                        values={informationSources ?? []}
                        onChange={(data) => setInformationSources(data)}
                        required={true}
                    />
                    {
                        othersIsChecked ?
                            <InputTextField
                                value={additionalInformationSources}
                                id={'additionalInformationSources'}
                                label={"Please input here"}
                                onChange={(e) => setAdditionalInformationSources(e.currentTarget.value)}
                            /> : <></>
                    }
                    <InputSubmit disabled={userStore?.configApplicationForm.isSubmitted}
                                 label={"Save"} onClick={onSubmitHandler}/>
                </form>
            </Grid>
        </>);
    });
};

