import React, {useEffect, useState} from 'react';
import {DropzoneDialog} from "material-ui-dropzone/dist";
import {
    Button,
    Card,
    CardActions,
    CardHeader,
    CardMedia,
    Dialog,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Divider,
} from "@material-ui/core";
import LoadingBackground from "../LoadingBackdrop";
import axios from 'axios';
import {API, FOR_UPLOAD_MEDIA, HOST_NAME} from "../../constants/api";
import {makeStyles} from "@material-ui/core/styles";
import {toast} from "react-toastify";
import cuid from "cuid";

const useStyles = makeStyles({
    root: {
        marginTop: '30px',
        marginBottom: '10px',
    },
    media: {
        height: 300,
    },
});

export enum AcceptedFileTypes {
    PDF = '.pdf',
    PNG = 'image/png',
    JPG = 'image/jpeg',
    IMAGE = 'image/*',
}

export enum ExampleTypeOptions {
    LINK,
    LOCAL_IMAGE,
    ASSETS_IMAGE,
}

interface IInputUploadMedia {
    onSubmitted?: (message: string, response?: string, error?: boolean) => void,
    fileName: string,
    filesLimit?: number;
    acceptedFileType?: AcceptedFileTypes,
    dialogTitle?: string,
    subHeader?: string,
    value?: string,
    showExampleButton?: boolean,
    exampleType?: ExampleTypeOptions,
    exampleData?: string,
    disabled?:boolean,
}

const InputUploadMedia: React.FC<IInputUploadMedia> = ({
                                                           onSubmitted,
                                                           fileName,
                                                           filesLimit = 1,
                                                           acceptedFileType = AcceptedFileTypes.IMAGE,
                                                           dialogTitle = "Dialog Title",
                                                           subHeader,
                                                           value,
                                                           showExampleButton = false,
                                                           exampleType = ExampleTypeOptions.LOCAL_IMAGE,
                                                           exampleData,
                                                           disabled,
}) => {
    const [open, setOpen] = useState<boolean>(false);
    const [openDialog, setOpenDialog] = useState<boolean>(false);
    const [loading, setLoading] = useState<boolean>(false);
    const [dialogClear, setDialogClear] = useState<boolean>(false);
    const [fileUrl, setFileUrl] = useState<string>('./apply/assets/images/white-background.png');
    const [exampleImageLocal, setExampleImageLocal] = useState<string>('./apply/assets/images/white-background.png');
    const classes = useStyles();

    useEffect(() => {
        setFileUrl(value ? `${HOST_NAME}${value}` : fileUrl);
    }, [value, fileUrl]);

    const onSaveHandler = async (files: any[]) => {
        if (files.length === 0) {
            toast.info('Please select or drag and drop your file.');
            return;
        }

        setOpen(false);
        setDialogClear(true);
        setLoading(true);

        const uid = localStorage.getItem('uid')?.toString();

        let formData = new FormData();
        formData.append('data', files[0]);
        formData.append('uid', uid ?? 'null');
        formData.append('file-name', fileName);
        formData.append('file-type', acceptedFileType.toString());

        try {
            const res = await axios.post(API + FOR_UPLOAD_MEDIA, formData, {
                headers: {'Content-Type': `multipart/form-data; boundary=${files[0]._boundary}`},
            });

            if (res.data.url)
                toast.success("Success uploading file.");
            else
                throw new Error('Fail uploading file.');

            if (onSubmitted) onSubmitted(res.data.message ?? null, `${res.data.url}?${cuid()}`, res.data.error);

        } catch (e) {
            console.log(e);
            toast.error("Fail uploading file.");
        }

        setLoading(false);

        setDialogClear(false);
    };

    const onCloseHandler = () => {
        setOpen(false);
    };

    const onOpenHandle = () => {
        setOpen(true);
    };

    const openFileHandler = () => {
        window.open(fileUrl);
    };


    const handleClickOpenDialog = () => {
        switch (exampleType) {
            case ExampleTypeOptions.LINK:
                window.open(exampleData);
                break;
            case ExampleTypeOptions.LOCAL_IMAGE:
                setExampleImageLocal('./apply/assets/images/white-background.png');
                setOpenDialog(true);
                break;
            case ExampleTypeOptions.ASSETS_IMAGE:
                setExampleImageLocal(`${exampleData}`);
                setOpenDialog(true);
                break;
        }
    };

    const handleCloseDialog = () => {
        setOpenDialog(false);
    };


    return (<>
        <LoadingBackground open={loading}/>

        <div style={{
            maxWidth: '80%',
            margin: "auto",
        }}>
            <Card className={classes.root}>
                <CardHeader
                    title={dialogTitle}
                    subheader={subHeader}
                />
                <Divider/>
                {
                    acceptedFileType === AcceptedFileTypes.PDF ? <></> :
                        <CardMedia
                            className={classes.media}
                            image={fileUrl}
                            title="Contemplative Reptile"
                        />
                }
                <Divider/>
                <CardActions>
                    <Button
                            disabled={disabled}
                            size="small"
                            color="primary"
                            variant={value ? 'contained' : 'outlined'}
                            onClick={onOpenHandle}
                    >
                        {value ? 'Update' : 'Select'} File
                    </Button>
                    <Button disabled={!value} size="small" color="secondary" onClick={openFileHandler}>
                        Open
                    </Button>
                    {
                        showExampleButton ?
                            <Button size="small" color="secondary" onClick={handleClickOpenDialog}>
                                Example
                            </Button> : <></>
                    }
                </CardActions>
            </Card>
        </div>
        {
            dialogClear ?
                <></> :
                <DropzoneDialog
                    open={open}
                    onSave={onSaveHandler}
                    acceptedFiles={[acceptedFileType.toString()]}
                    showPreviews={true}
                    maxFileSize={1000000}
                    filesLimit={filesLimit}
                    onClose={onCloseHandler}
                    dialogTitle={dialogTitle}
                />
        }
        <Dialog
            open={openDialog}
            keepMounted
            onClose={handleCloseDialog}
            aria-labelledby="example-dialog-title"
            aria-describedby="example-dialog-description"
        >
            <DialogTitle id="example-dialog-title">{dialogTitle}</DialogTitle>
            <DialogContent>
                <DialogContentText id="example-dialog-description">
                    <img
                        alt={'Assets Example local'}
                        src={exampleImageLocal}/>
                </DialogContentText>
            </DialogContent>
        </Dialog>
    </>);
};

export default InputUploadMedia;