import React, {FormEventHandler, useContext, useEffect, useState} from "react";
import {Divider, FormControl, FormControlLabel, FormLabel, Grid, Radio, RadioGroup} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Autocomplete, {createFilterOptions} from "@material-ui/lab/Autocomplete";
import {countries, tShirtOptions} from "../../constants";
import {ITShirtOptionType} from "../../constants/interfaces";
import LoadingBackground from "../LoadingBackdrop";
import {toast} from "react-toastify";
import {useObserver} from 'mobx-react';
import InputSubmit from "../forms/InputSubmit";
import {UserStoreContext} from "../../providers/UserProvider";
import InputPhone from "../forms/InputPhone";
import InputAutoCompleteForCountry from "../forms/InputAutoCompleteForCountry";

const filter = createFilterOptions();

export const PersonalDetails: React.FC = () => {
    const userStore = useContext(UserStoreContext);
    const [loading, setLoading] = useState<boolean>(userStore?.loading ?? false);

    const [fullName, setFullName] = useState<string | undefined>();
    const [nickName, setNickName] = useState<string | undefined>();
    const [address, setAddress] = useState<string | undefined>();
    const [city, setCity] = useState<string | undefined>();
    const [province, setProvince] = useState<string | undefined>();
    const [country, setCountry] = useState<string | undefined>();
    const [postcode, setPostcode] = useState<string | undefined>();
    const [phone, setPhone] = useState<string | undefined>();
    const [mobilePhone, setMobilePhone] = useState<string | undefined>();
    const [waNumber, setWaNumber] = useState<string | undefined>();
    const [facebook, setFacebook] = useState<string | undefined>();
    const [instagram, setInstagram] = useState<string | undefined>();
    const [socialMedia, setSocialMedia] = useState<string | undefined>();
    const [tShirtSize, setTShirtSize] = useState<ITShirtOptionType | null>(null);
    const [dateOfBirth, setDateOfBirth] = React.useState<Date | undefined>();
    const [sex, setSex] = useState<string>('female');
    const [maritalStatus, setMaritalStatus] = useState<string>('single');
    const [religion, setReligion] = useState<string | undefined>();
    const [passportNumber, setPassportNumber] = useState<string | undefined>();
    const [expiryDate, setExpiryDate] = React.useState<Date | undefined>();

    const [nationality, setNationality] = useState<string | undefined>();

    useEffect(() => {
        if (!userStore?.loading) {
            setFullName(userStore?.personalDetails?.fullName);
            setNickName(userStore?.personalDetails?.nickName);
            setAddress(userStore?.personalDetails?.address);
            setCity(userStore?.personalDetails?.city);
            setProvince(userStore?.personalDetails?.province);
            setCountry(userStore?.personalDetails?.country ?? countries[99].label);
            setPostcode(userStore?.personalDetails?.postcode);
            setPhone(userStore?.personalDetails?.phone);
            setMobilePhone(userStore?.personalDetails?.mobilePhone);
            setWaNumber(userStore?.personalDetails?.waNumber);
            setFacebook(userStore?.personalDetails?.facebook);
            setInstagram(userStore?.personalDetails?.instagram);
            setSocialMedia(userStore?.personalDetails?.socialMedia);
            setTShirtSize({size: userStore?.personalDetails?.tShirtSize ?? ''});
            setDateOfBirth(userStore?.personalDetails?.dateOfBirth);
            setSex(userStore?.personalDetails?.sex ?? 'female');
            setMaritalStatus(userStore?.personalDetails?.maritalStatus ?? 'single');
            setReligion(userStore?.personalDetails?.religion);
            setPassportNumber(userStore?.personalDetails?.passportNumber);
            setExpiryDate(userStore?.personalDetails?.expiryDate);
            setNationality(userStore?.personalDetails?.nationality ?? countries[99].label);
        }
    }, [loading, userStore]);


    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();

        // if (checkExpiredDate()) {
        //     toast.info("Please check again your expiry date field");
        //     return;
        // }

        if ((mobilePhone?.length ?? 0) < 3) {
            toast.info("Please check again your phone number");
            return;
        }

        if (!e?.currentTarget?.form?.reportValidity()) {
            toast.info("Please complete all required data");
            return;
        }

        setLoading(true);
        const res = await userStore?.updatePersonalDetails({
            fullName,
            nickName,
            address,
            city,
            province,
            country,
            postcode,
            phone,
            mobilePhone,
            waNumber,
            facebook,
            instagram,
            socialMedia,
            tShirtSize: tShirtSize?.size,
            dateOfBirth,
            sex,
            maritalStatus,
            religion,
            passportNumber,
            expiryDate,
            nationality
        });
        if (res?.hasError()) {
            toast.error(res?.error?.message);
            setLoading(false);
        } else {
            toast.success("Success updating Personal Detail's");
        }

        setLoading(false);
    };

    // const checkExpiredDate = (): boolean =>
    //     ((expiryDate?.getMonth() ?? 7) < 8);


    return useObserver(() => {
        if (userStore?.loading)
            return <LoadingBackground open={true}/>;

        return (<>
            {
                // @ts-ignore}
                <LoadingBackground open={loading || userStore.loading}/>
            }
            <Grid item>
                <Typography variant="h4" gutterBottom>
                    Personal Details
                </Typography>
                <Divider/>
                <form onSubmit={onSubmitHandler} style={{width: '80%', textAlign: 'center'}} noValidate={false}>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Full name"
                        id="fullName"
                        name="fullName"
                        autoComplete="fullName"
                        value={fullName ?? ''}
                        onChange={(e) => setFullName(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Known as (if different)"
                        id="nickName"
                        name="nickName"
                        autoComplete="nickName"
                        value={nickName ?? ''}
                        onChange={(e) => setNickName(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Address (please detail)"
                        id="address"
                        name="address"
                        autoComplete="address"
                        multiline={true}
                        value={address ?? ''}
                        onChange={(e) => setAddress(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="City"
                        id="city"
                        name="city"
                        autoComplete="city"
                        value={city ?? ''}
                        onChange={(e) => setCity(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="State/Province"
                        id="province"
                        name="province"
                        autoComplete="province"
                        value={province ?? ''}
                        onChange={(e) => setProvince(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        label="Postcode"
                        id="postcode"
                        name="postcode"
                        autoComplete="postcode"
                        value={postcode ?? ''}
                        onChange={(e) => setPostcode(e.currentTarget.value)}
                    />


                    <InputAutoCompleteForCountry
                        label={"Country"}
                        value={country ?? countries[99].label}
                        onChange={(e: any, v: any) => setCountry(v?.label)}
                    />

                    <InputPhone
                        required
                        labels={'Mobile Phone number (please include the country code)'}
                        value={mobilePhone ?? ''}
                        onChange={setMobilePhone}
                    />
                    <InputPhone
                        labels={'Phone number (please include the country code)'}
                        value={phone ?? ''}
                        onChange={setPhone}
                    />
                    <InputPhone
                        labels={'WhatsApp number (please include the country code)'}
                        value={waNumber ?? ''}
                        onChange={setWaNumber}
                    />

                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Facebook (if any)"
                        id="facebook"
                        name="facebook"
                        autoComplete="facebook"
                        value={facebook ?? ''}
                        onChange={(e) => setFacebook(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Instagram (if any)"
                        id="instagram"
                        name="instagram"
                        autoComplete="instagram"
                        value={instagram ?? ''}
                        onChange={(e) => setInstagram(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Another social media"
                        id="socialMedia"
                        name="socialMedia"
                        autoComplete="socialMedia"
                        value={socialMedia ?? ''}
                        onChange={(e) => setSocialMedia(e.currentTarget.value)}
                    />
                    <Autocomplete
                        onChange={(event: any, newValue: ITShirtOptionType | null) => {
                            if (newValue && newValue.inputValue) {
                                setTShirtSize({
                                    size: newValue.inputValue,
                                });
                                return;
                            }

                            setTShirtSize(newValue);
                        }}
                        filterOptions={(options, params) => {
                            const filtered = filter(options, params) as ITShirtOptionType[];

                            if (params.inputValue !== '') {
                                filtered.push({
                                    inputValue: params.inputValue,
                                    size: `Add "${params.inputValue}"`,
                                });
                            }

                            return filtered;
                        }}
                        id="T-Shirt-Size"
                        options={tShirtOptions as ITShirtOptionType[]}
                        getOptionLabel={option => {
                            // e.g value selected with enter, right from the input
                            if (typeof option === 'string') {
                                return option;
                            }
                            if (option.inputValue) {
                                return option.inputValue;
                            }
                            return option.size;
                        }}
                        renderOption={option => option.size}
                        style={{width: '100%', marginTop: 16, marginBottom: 8}}
                        freeSolo
                        disableClearable={true}
                        value={tShirtSize}
                        renderInput={params => (
                            <TextField
                                {...params}
                                required
                                fullWidth
                                label="T-Shirt (Asian size)" variant="outlined"/>
                        )}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        id="date-of-birth"
                        label="Date of birth"
                        type="date"
                        value={dateOfBirth?.toISOString().split('T')[0] ?? ''}
                        onChange={(e) => setDateOfBirth(new Date(e.currentTarget.value))}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                    <FormControl
                        component="fieldset"
                        style={{width: '100%', marginTop: 16, marginBottom: 8, textAlign: 'left'}}
                    >
                        <FormLabel component="legend">Gender</FormLabel>
                        <RadioGroup
                            aria-label="sex"
                            name="sex"
                            value={sex}
                            onChange={(e) => setSex(e.currentTarget.value)}
                        >
                            <FormControlLabel value="female" control={<Radio/>} label="Female"/>
                            <FormControlLabel value="male" control={<Radio/>} label="Male"/>
                        </RadioGroup>
                    </FormControl>

                    <FormControl
                        component="fieldset"
                        style={{width: '100%', marginTop: 16, marginBottom: 8, textAlign: 'left'}}
                    >
                        <FormLabel component="legend">Marital status</FormLabel>
                        <RadioGroup
                            aria-label="maritalStatus"
                            name="maritalStatus"
                            value={maritalStatus}
                            onChange={(e) => setMaritalStatus(e.currentTarget.value)}
                        >
                            <FormControlLabel value="single" control={<Radio/>} label="Single"/>
                            <FormControlLabel value="married" control={<Radio/>} label="Married"/>
                        </RadioGroup>
                    </FormControl>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        label="Religion (if any)"
                        id="religion"
                        name="religion"
                        autoComplete="religion"
                        value={religion ?? ''}
                        onChange={(e) => setReligion(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Passport number"
                        id="passportNumber"
                        name="passportNumber"
                        autoComplete="passportNumber"
                        value={passportNumber ?? ''}
                        onChange={(e) => setPassportNumber(e.currentTarget.value)}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        required
                        id="expiry-date"
                        label="Expiry date (Passport)"
                        type="date"
                        value={expiryDate?.toISOString().split('T')[0] ?? ''}
                        onChange={(e) => setExpiryDate(new Date(e.currentTarget.value))}
                        InputLabelProps={{
                            shrink: true,
                        }}
                        // error={checkExpiredDate()}
                    />
                    <InputAutoCompleteForCountry
                        label={"Nationality"}
                        value={nationality ?? countries[99].label}
                        onChange={(e: any, v: any) => setNationality(v?.label)}
                    />
                    <InputSubmit disabled={userStore?.configApplicationForm.isSubmitted}
                                 label={"Save"} onClick={onSubmitHandler}/>
                </form>
            </Grid>
        </>);
    });
};