import React, {useContext, useState} from 'react';
import {createStyles, Divider, Icon, IconButton, makeStyles, Menu, MenuItem, Typography} from '@material-ui/core';
import {NavLink, useHistory} from 'react-router-dom';
import {SETTINGS, SIGN_IN} from "../constants";
import {FirebaseContext} from "../firebase";
import {UserStoreContext} from "../providers/UserProvider";

const useStyles = makeStyles(() => createStyles({
    resetLinkStyleApppBar: {
        textDecoration: 'none',
        color: 'white'
    },
    resetLinkStyleMenu: {
        textDecoration: 'none',
        color: 'black'
    }
}));

export default function MenuNavigation() {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const firebaseApp = useContext(FirebaseContext);
    const userStore = useContext(UserStoreContext);
    const history = useHistory();
    const [emailUser] = useState<string>(localStorage.getItem('email') ?? '');

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const signOutHandler = async () => {
        setAnchorEl(null);

        const res = await firebaseApp?.doSignOut();
        if (!res?.hasError()) {
            userStore?.clear();
            history.push(SIGN_IN);
        }
    };

    return (
        <>
            <Typography variant={"subtitle2"} style={{cursor: 'default'}}>
                {emailUser}
            </Typography>
            <IconButton
                aria-owns={Boolean(anchorEl) ? "menu-appbar" : undefined}
                aria-haspopup="true"
                color="inherit"
                onClick={handleClick}
            >
                <Icon>account_circle</Icon>
            </IconButton>

            <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                    vertical: "top",
                    horizontal: "right"
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "right",
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <NavLink className={classes.resetLinkStyleMenu} to={SETTINGS}>
                    <MenuItem onClick={handleClose}>
                        Settings
                    </MenuItem>
                </NavLink>
                <Divider/>
                <MenuItem onClick={signOutHandler}>
                    Sign Out
                </MenuItem>
            </Menu>
        </>
    );
}