import React from 'react';
import TextField from "@material-ui/core/TextField";
import {FilledInputProps} from "@material-ui/core/FilledInput";

interface IInputTextField {
    value?: string,
    label: string,
    helperText?: string,
    id?: string,
    onChange?: FilledInputProps['onChange'];
    required?: boolean,
    multiline?: boolean,
    rows?: number,
    rowsMax?: number,
    error?: boolean,
    type?: string,
}

const InputTextField: React.FC<IInputTextField> = ({
                                                       value,
                                                       label,
                                                       id,
                                                       required = false,
                                                       multiline = false,
                                                       helperText,
                                                       onChange,
                                                       rows = 1,
                                                       rowsMax = 1,
                                                       error,
                                                       type
                                                   }) => {
    return (<TextField
        variant="outlined"
        margin="normal"
        fullWidth
        required={required}
        label={label}
        id={id}
        name={id}
        autoComplete={id}
        multiline={multiline}
        helperText={helperText}
        value={value ?? ''}
        onChange={onChange}
        rows={rows}
        rowsMax={rowsMax}
        error={error}
        type={type}
    />);
};

export default InputTextField;