import React from 'react';
import {FilledInputProps} from "@material-ui/core/FilledInput";
import {FormControl, FormControlLabel, FormLabel, Radio, RadioGroup} from "@material-ui/core";
import {IInputRadioGroupData} from "../../constants/interfaces";
import cuid from "cuid";

interface IInputRadioGroup {
    value?: string,
    label: string,
    id?: string,
    onChange?: FilledInputProps['onChange'];
    required?: boolean,
    options: IInputRadioGroupData[],
}

const InputRadioGroup: React.FC<IInputRadioGroup> = ({
                                                         id,
                                                         label,
                                                         value,
                                                         options,
                                                         required = false,
                                                         onChange,
                                                     }) => {

    return (<FormControl
        component="fieldset"
        required={required}
        style={{width: '100%', marginTop: 16, marginBottom: 8, textAlign: 'left'}}
    >
        <FormLabel component="legend">{label}</FormLabel>
        <RadioGroup
            aria-label={id}
            name={id}
            value={value}
            onChange={onChange}
        >
            {options.map((data) => (
                <FormControlLabel
                    key={cuid()}
                    value={data.value}
                    control={<Radio/>}
                    label={data.label}
                />
            ))}
        </RadioGroup>
    </FormControl>);
};

export default InputRadioGroup;